import React from 'react';
import './Navigation.css';
import { NavLink } from 'react-router-dom';
export const Navigation = () => {
    return (
        <div className='hs-navigation'>
            
            <div className='hs-navigation-inner-wrapper'>
                <ul className='clearfix'>
                <li className='hs-menu-item'>
                        <NavLink to='/' exact activeClassName='active'>Overview</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Profile' exact activeClassName='active'>Profile</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Experience' activeClassName='active'>Experience</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Education' activeClassName='active'>Education</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Author_Bio' activeClassName='active'>contact</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/' activeClassName='active'>live project</NavLink>
                    </li>
                </ul>
            </div>
        </div>
    );
}