import React from 'react';
export const Education = () => {
    return (
        <>
            <div className='hs-education'>
                <h2 className='title'>
                    <i className="fa fa-graduation-cap" aria-hidden="true"> </i>
                    <span className='text'>Education</span>
                </h2>
                <h4 className='edu-strem'>
                    <span className='text'> Arts</span>
                </h4>
                <h5 className='university-name'>University of Delhi</h5>
                <p className='hs-date-location-wrapper d-flex flex-wrap justify-content-between'>
                    <span className='date'>
                        <i className="fa fa-calendar" aria-hidden="true">
                        </i>
                        <span className='text'>2010-2013</span>
                    </span>
                    <span className='location'><i class="fa fa-map-marker" aria-hidden="true"></i>
                        <span className='text'> Delhi </span>
                    </span>
                </p>
            </div>
            <div className='hs-languages'>
                <h2 className='title'>
                    <i className="fa fa-language" aria-hidden="true"></i> languages </h2>
                <p className='lang'> english <br />
                    <span> native </span>
                </p>
                <p className='lang'> hindi <br />
                    <span> native </span>
                </p>
            </div>
            <div className='hs-skils'>
                <h2 className='title'><i className="fa fa-tachometer" aria-hidden="true"></i> Technical Skils </h2>
                <p className='d-flex flex-wrap justify-content-start skils-tags'>
                    <span>node js</span>
                    <span> jQuery </span>
                    <span>javascript</span>
                    <span> react </span>
                    <span>mongodb</span>
                    <span> express </span>
                </p>
            </div>
        </>
    );
}