import React from 'react';
import { Profile } from '../Components/Profile';
import { Author_bio } from '../Components/Author_bio';
import { Education } from '../Components/Education';
import { Experience } from '../Components/Experience';

export const Overview = () => {
    return (
        <>
            <div className='d-flex flex-wrap flex-row'>
                <div className='hs-profile-wrapper col-xs-12 col-sm-12 col-md-4 col-lg-4 col-xl-4'>
                    <Profile />
                </div>
                <div className='hs-author-bio-wrapper col-xs-12 col-sm-12 col-md-8 col-lg-8 col-xl-8'>
                    <Author_bio />
                </div>
            </div>
            <div className='d-flex flex-wrap flex-row'>
                <div className='hs-education-wrapper col-xs-12 col-sm-12 col-md-4 col-lg-4 col-xl-4'>
                    <Education />
                </div>
                <div className='hs-experience-wrapper col-xs-12 col-sm-12 col-md-8 col-lg-8 col-xl-8'>
                    <Experience />
                </div>

            </div>
        </>
    );
}