import React from 'react';
import { NavLink } from 'react-router-dom';
export const Experience = () => {
    return (
        <>
            <div className='hs-experience'>
                <h2 className='title'><i className="fa fa-building" aria-hidden="true"></i> Experience</h2>
                <p className='experience-tags'>Add Experience</p>
                <p className='experience-tags'>Add Experience</p>
            </div>
            <div className='hs-live-project'>
                <h2 className='title'><i className="fa fa-globe" aria-hidden="true"></i> Live Project</h2>
                <ul>
                    <li className=''>
                        <i className="fa fa-arrow-right" aria-hidden="true"></i> <NavLink to='/' exact activeClassName='active'>Demo</NavLink>
                    </li>
                </ul>
            </div>
        </>
    );
}